p {
  padding: 0px;
  margin: 0px;
  font-size: 14px;
}
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #16366452;
}

/* Handle */
::-webkit-scrollbar-thumb {
  padding-left:2px;
  padding-right: 2px;
  background: #1237679f;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #2c4b73;
}
button {
  border: 1px solid #fff;
  font-size: 12px;
  margin: 0;
  padding: 5px 10px;
  background-color: #123767;
  border-radius: 0px;
  border-color: #77aaeb;
  color: #c7e0ff;
}
button:hover {
  box-shadow: 0 0 10px 0px inset #00b8ff75;
  cursor: pointer;
}
select {
  background-color: #123767;
  border: 1px solid #77aaeb;
  border-radius: 0;
  color: #c7e0ff;
  font-size: 12px;
  margin: 0;
  padding: 5px 10px;
}

element.style {
  width: 122px;
  height: 63px;
}
textarea {
  background-color: #123767;
  border: 1px solid #77aaeb;
  border-radius: 0;
  color: #c7e0ff;
  font-size: 12px;
  margin: 0;
  padding: 5px 10px;
  width: 100%!important;
}
input {
  border: 1px solid #fff;
  font-size: 12px;
  margin: 0;
  padding: 5px 10px;
  background-color: #123767;
  border-radius: 0px;
  border-color: #77aaeb;
  color: #c7e0ff;
}

.hidden {
  display: none;
}

.visible {
  display: block;
}


.scoreboard {
  background: #081527e6;
  border: 1px solid #2c4b73;
  color: #fff;
  width: 40%;
  margin: 0 auto;
  margin-top: 5%;
}
.container {
  padding: 15px;
}
.header {
  width: 100%;
  justify-content: space-between;
  display: flex;
  margin-bottom: 15px;
}

.close {
margin-left: 5px
}


.navigation {
  display: flex;
  width: 100%;
}

.menu-btn {
  margin-right: 5px;
}

.menu {
  width: 100%;
}

.search {
  width: 100%;
  display: flex;
}

.search-input {
  width: 100%;
  margin-right: 5px;
}

.body {
  height: 100%;
  margin-top: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  row-gap: 10px;
  column-gap: 5px;
}

.player-data {
  display: flex;
  justify-content: center;
  width: 31%;
  border: 1px solid #77aaeb;
  font-size: 12px;
  padding: 2px;
}

.player {
display: flex;
justify-content: space-between;
width: 100%;

}

.player-container {
  column-gap: 10px;
  display: flex;
  flex-wrap: wrap;
  max-height: 160px;
  width: 100%;
  overflow: auto;
  row-gap: 10px;
}
.name {
  width: 100%;
  display: flex;
  align-items: center;
  padding-left: 5px;
  padding-right: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #c7e0ff;
}

.id {
  background: #123767;
  border: 1px solid #77aaeb;
  display: flex;
  justify-content: center;
  min-width: 20px;
  padding-right: 2px;
  color: #c7e0ff;
}

.society-data {

}

.title {
  font-weight: 600;
  font-size: 16px;
}

.society-data-layout {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 285px;
  justify-content: space-between;
  margin-top: 15px;
  overflow: auto;
  padding-right: 7px;
  row-gap: 15px;
  width: 100%;
}


.business {
  display: flex;
  width: 49%;
  height: fit-content;;
}

.business-infodec {
display: flex;
flex-direction: column;
width: 100%!important;
}

.business-body {
  background: #12376747;
  border: 1px solid #2c4b73;
  display: flex;
  flex-direction: row;
  padding: 5px;
  row-gap: 15px;
  width: 100%;
}

.business-location {
  display: flex;
  flex-direction: row;
  font-size: 14px;
  margin-top: 10px;
  row-gap: 5px;
  align-items: center;
  justify-content: space-between;
}

.business-info {

}

.business-description {
  font-size: 12px;
  overflow: auto;
}

.business-name {
  font-weight: 600;
  margin-bottom: 8px;
  text-transform: uppercase;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;
}

.business-edit {
    margin-top: 10px;
    column-gap: 15px;
    display: flex;
}

.business-cap {
  font-style: italic;
  font-size: 12px;
}

.business-status-icon {
  margin-left: 8px;
  width: 40px;
  height: 12px;
  transition: fill 0.3s ease, box-shadow 0.3s ease;
}

.business-status-icon.closed {
  fill: rgb(0, 13, 42);
  box-shadow: 0 0 10px rgba(0, 13, 42, 0.5);
  border: 1px solid #2c4b73;
}

.business-status-icon.open {
  fill: rgb(2, 245, 172);
  box-shadow: 0 0 10px rgba(2, 245, 172, 0.5);
  border: 1px solid rgb(49, 157, 125);
}

.counts {
  display: flex;
  flex-direction: row;
  font-size: 14px;
  width: 30%;
  justify-content: space-between;
}


.business-img {
  object-fit: cover;
  border: 1px solid #2c4b73;
  border-right: none;
  width: 100px;
}

.player-body {
  width: 100%;
    display: flex;
    justify-content: space-between;
}

.edit-business-form {
  display: flex;
  flex-wrap: wrap;
  column-gap: 15px;
  row-gap: 15px;
  flex-direction: row;
  align-content: center;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
  border: 1px solid white;
  background: #12376747;
  border: 1px solid #2c4b73;
}

.add-business-form {
  display: flex;
    flex-wrap: wrap;
    column-gap: 15px;
    row-gap: 15px;
    flex-direction: row;
    align-content: center;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
    border: 1px solid white;
    background: #12376747;
    border: 1px solid #2c4b73;
}

.confirmation-dialog {
  display: flex;
  background: #081527e6;
  border-top: 1px solid #2c4b73;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.dialog-content button {
  margin-right: 20px;
  width: 40%;
}
.dialog-content p + button {
  margin-top: 10px;
}

.dialog-content {
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  padding: 10px;
  flex: 1 0 1%;
  text-align: center;
}